*{
    margin: 0;
    padding: 0;
}

body{
    background-image: url('https://png.pngtree.com/thumb_back/fh260/background/20240522/pngtree-abstract-blue-orange-background-with-smooth-gradient-image_15683688.jpg');
    background-size: cover;
}

.app{
    text-align: center;
    color: blue;
}

h1{
    color: rgb(51, 17, 114);
}
.app input{
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid grey;
    outline: none;
    padding: 10px 5px;
    width: 200px;
    margin-top: 10px;
}

.app button{
    background-color: rgb(63, 44, 4);
    color: white;
    border: none;
    outline: none;
    margin-top: 10px;
    padding: 10px 30px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 20PX;
}

.navActive{
    color: rgb(39, 7, 92);
    font-size: 20px;
    font-weight: bold;
}
nav{
    background-color: rgb(209, 17, 17);
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;
    margin-left: 25%;
    width: 50%;
}
nav ul{
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    align-items:flex-start;
    width: 100%;
    color: rgb(236, 224, 224);
    font-weight: bold;
    cursor: pointer;
    margin-left: 10px;
    margin-right: 10px;
}
nav ul li{
    font-size: 18px;
    transition: all 0.5s ease-in-out;
}

.app-container h3{
    text-align: center;
    margin-top: 25px;
    color: rgb(43, 6, 6);
}

.start{
    margin-top: 10%;
}